:root {
    color-scheme: light;
    /* colors: */
    --main: hsl(0, 100%, 33%);
    --main-2: hsl(5, 90%, 22%);

    --text-color: hsl(0, 0%, 0%);
    --secondary-text-color: hsl(0, 0%, 15%);

    --bg: hsl(180, 30%, 90%);
    --secondary-bg: hsl(180, 10%, 75%);
    --tertiary-bg: hsl(180, 10%, 60%);

    --contrast: hsl(195, 100%, 26%);
    --contrast-2: hsl(240, 100%, 50%);
}
